      <tr>
      <td>id</td>
      <td>姓名</td>
      <td>班级</td>
      <td>单位</td>
      <td>操作</td>
      </tr>
      <tr v-for="item in datas">
        <td>{{item.id}}</td>
        <td>{{item.name}}</td>
        <td>{{item.age}}</td>
        <td>{{item.classes}}</td>
        <td>
          <a href="#/edit">编辑</a>
        </td>
      </tr>
    </table>
    <a href="#/add" >添加</a>
  </div>


</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      datas:[
          {id:1,name:"张三",age:"12",classes:"0232",},
          {id:2,name:"张三",age:"12",classes:"0232",},
          {id:3,name:"张三",age:"12",classes:"0232",}
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

</style>



